import React from "react";
import AnimLetter from "../Animation/AnimationLetter";
import ScrollDown from "../Scroll/ScrollDown";
import { useMediaQuery } from 'react-responsive';

import {
    AboutContainer,
    AboutTextWrapper,
    AboutText,
    SkillContainer
} from './AboutStyle'
import { Section, Title } from "../../utils/globalSection";
import Cloud from "./Cloud";

export default function About() {
    const isMobile = useMediaQuery({ maxWidth: 450 });

    const showCloud = !isMobile

    return (
        <Section>
            <AboutContainer>
                <ScrollDown />
                <AboutTextWrapper>
                    <header>
                        <Title>
                            <AnimLetter text=" Propos" />
                        </Title>
                        <AboutText>
                            Aprs une carrire dans l'arme franaise en tant
                            que parachutiste, j'ai dcid de me reconvertir dans
                            le domaine du dveloppement web en 2020. J'ai donc
                            repris mes tudes avec l'cole WIS de Toulouse pour
                            valider un BTS DWWM ainsi qu'une Licence Chef de
                            Projet. J'ai commenc un Master en Ingnierie
                            Informatique en 2022 pour une alternance de deux
                            ans, aprs avoir trouv une alternance dans le
                            secteur bancaire, j'ai eu une proposition de CDI
                            dans le domaine de l'Assurance, que j'ai
                            immdiatement accept. En effet, vivre de ma passion
                            tait mon objectif depuis le dbut de ma
                            reconversion. J'ai donc arrt mon Master en cours
                            de premire anne sans le valider. Et c'est donc
                            dans le domaine de l'assurance que je reprends les
                            armes, mais cette fois assis sur une chaise de
                            bureau afin de grer les flux EDI d'une entreprise
                            qui gnre des millions de chiffre d'affaires. Me
                            voil enfin Dveloppeur Web full stack dans une
                            technologie que je privilgie comme PHP.
                        </AboutText>
                        <AboutText>
                            Depuis que j'ai dcouvert le dveloppement
                            informatique, je n'ai jamais cess d'apprendre
                            chaque jour, pouss par la curiosit et une passion
                            grandissante.
                        </AboutText>
                    </header>
                </AboutTextWrapper>
                {isMobile ? null : (
                    <SkillContainer data-aos="fade-down">
                        <Cloud showCloud={showCloud}/>
                    </SkillContainer>
                )}
            </AboutContainer>
        </Section>
    )
}